<template>
	<div class="fansManagement">
		<div class="fansManagement_top">
			<div class="fansManagement_title">
				粉丝管理
			</div>
			<div>
				<Button type="primary" class="mgleft_15" :disabled="!judgeNum"
					@click="handSynchronizeFans">同步粉丝</Button>
			</div>
		</div>

		<div class="fansManagement_in">
			<div class="fansManagement_in_item">
				<!-- <span>类型</span> -->
				<Select v-model="state" style="width:200px" @on-change="handgetWxFansList">
					<Option v-for="item in stateList" :value="item.value" :key="item.value">{{ item.label }}</Option>
				</Select>
			</div>
			<div class="fansManagement_in_item">
				<!-- <span>微信号/手机号/微信昵称</span> -->
				 <Input v-model="search" placeholder="请输入微信号/手机号/微信昵称" style="width: 300px" />
			</div>
			<div class="fansManagement_in_item_border">
				<!-- <span>时间</span> -->
				<!-- <Select v-model="type" style="width:150px;text-align: center;" @on-change="handgetWxFansList">
					<Option v-for="item in typeList" :value="item.value" :key="item.value">{{ item.label }}</Option>
				</Select> -->
				<DatePicker type="daterange" :value="startTime" format="yyyy-MM-dd" @on-change="getStartTime"
					placeholder="请选择时间" style="width: 210px;padding-right: 10px;">
				</DatePicker>
				<Button type="primary" @click="handgetWxFansList">搜索</Button>
			</div>
			<!-- <div class="fansManagement_in_item">
				<DatePicker type="daterange" :value="startTime" format="yyyy-MM-dd" @on-change="getStartTime" 
				placeholder="请选择时间" style="width: 210px;padding-right: 10px;">
				</DatePicker>
				
				<DatePicker type="date" :value="endTime" format="yyyy-MM-dd" @on-change="getEndTime" placeholder="结束时间"
					style="width: 200px;padding-left: 10px;"></DatePicker>
				<Button type="primary" @click="handgetWxFansList">搜索</Button>
			</div> -->
		</div>
		<div class="fansManagement_bot">
			<Table :columns="columns1" :data="data1" ref="tableFams" :height="tableHeight">
				<template slot-scope="{ row }" slot="img">
					<div style="display: flex;align-items: center;text-align: left;">
						<div class="mgright_15">
							<Avatar :src="row.portrait" />
						</div>
						<div>{{row.nickname}}</div>
					</div>
					<!-- <img :src="row.portrait" width="50" height="50" /> -->
				</template>
				<template slot-scope="{ row }" slot="follow">
					<div v-if="row.followState == 0" class="color_Warning">未知</div>
					<div v-if="row.followState == 1" class="color_theme">关注</div>
					<div v-if="row.followState == 2" class="color_Error">取关</div>
				</template>
				<template slot-scope="{ row }" slot="isGetCustomerData">
					<div v-if="row.isGetCustomerData == 0" class="color_Warning">未获取</div>
					<div v-if="row.isGetCustomerData == 1" class="color_theme">已获取</div>
				</template>


				<template slot-scope="{ row }" slot="isReply">
					<i-switch v-model="row.isReply" @on-change="handIsReplyChange($event,row)"></i-switch>
				</template>
				<template slot-scope="{ row }" slot="isPursuit">
					<i-switch v-model="row.isPursuit" @on-change="handIsPursuitChange($event,row)"></i-switch>
				</template>
				<template slot-scope="{ row }" slot="action">
					<Button size="small" class="list_Button" @click="handgetFansAbnormalMsg(row)">异常消息</Button>
					<Button size="small" class="list_Button" @click="handchatRecord(row)">对话记录</Button>
				</template>
			</Table>
		</div>

		<Modal v-model="fansMsgModal" title="异常消息" width="1200" @on-ok="handfansMsgModal" @on-cancel="handfansMsgModal">
			<Table :columns="fansMsgColumns" border :data="fansMsglist" height="607"></Table>
			<div style="text-align: center; margin-top: 20px;padding-bottom: 20px;">
				<Page :total="fansMsgTotal" :page-size="fansMsgLimit" show-total show-elevator
					@on-page-size-change="handleCurrentChangeMsg" @on-change='handleSizeChangeMsg' />
			</div>
		</Modal>

		<ChatBox ref="chatbox"></ChatBox>
		<div style="text-align: center; margin-top: 20px;padding-bottom: 20px;">
			<Page :total="total" :page-size="limit" show-total show-elevator @on-page-size-change="handleSizeChange"
				@on-change='handleCurrentChange' />
		</div>

	</div>
</template>

<script>
	import {
		indexApiModules
	} from '@/api';
	export default {
		data() {
			return {
				model1: '',
				typeList: [{
						value: 1,
						label: '回复时间'
					},
					{
						value: 2,
						label: '关注时间'
					},
				],
				stateList: [{
						value: 0,
						label: '全部客资'
					},
					{
						value: 1,
						label: '未获取客资'
					},
					{
						value: 2,
						label: '已关注'
					},
				],

				columns1: [{
						title: '排序',
						type: 'index',
						align: 'center'
					},
					// {
					// 	title: '微信昵称',
					// 	key: 'nickname',
					// 	align: 'center'
					// },
					{
						title: '微信',
						key: 'age',
						slot: 'img',
						// align: 'center',
						width: 250,
					},
					{
						title: '是否关注',
						slot: 'follow',
						align: 'center'
					},
					{
						title: '关注时间',
						key: 'subscribeTime',
						width: 170,
						align: 'center'
					},
					{
						title: '互动时间',
						key: 'updateTime',
						width: 170,
						align: 'center',
						render: (h, params) => {
							 if (params.row.updateTime) {
									return h('span', params.row.updateTime);
								}else {
									return h('span', '暂无');
								}
						}
					},
					{
						title: '客资状态',
						align: 'center',
						slot: 'isGetCustomerData',
					},
					{
						title: '智能回复',
						align: 'center',
						slot: 'isReply'
					}, {
						title: '自动追粉',
						align: 'center',
						slot: 'isPursuit'
					}, {
						title: '消息',
						key: 'messageNum',
						align: 'center'
					},
					{
						title: '异常消息',
						key: 'abnormalMessageNum',
						align: 'center'
					},
					{
						title: '操作',
						slot: 'action',
						width: 200,
						align: 'center'
					}
				],

				data1: [],
				wxAppId: localStorage.getItem('wxAppId'),
				startTime: '',
				endTime: '',
				search:'',
				type: 1,
				state: 0,
				page: 1, //页码
				limit: 15, // 每页的数量,
				total: 0,

				fansMsgColumns: [{
						title: '排序',
						type: 'index',
						align: 'center'
					},
					{
						title: '时间',
						key: 'creationTime',
						align: 'center'
					},
					{
						title: '内容',
						key: 'content',
						align: 'center'
					},
				],
				fansMsgModal: false,
				fansMsglist: [],
				fansMsgPage: 1,
				fansMsgLimit: 15,
				fansMsgTotal: 0,


				judgeNum: 0,
				tableHeight: 607,

			}
		},
		mounted() {
			window.addEventListener('resize', this.getTableHeight);
			this.getTableHeight();

			this.handgetWxFansList()
			this.handjudgeSynchronize()
		},
		methods: {
			getTableHeight() {
				console.log(this.$refs.tableFams);
				if (!this.$refs.tableFams) {
					return
				}
				this.tableHeight = window.innerHeight - this.$refs.tableFams.$el.offsetTop - 200;
			},

			// 同步粉丝
			handSynchronizeFans() {
				indexApiModules.synchronizeFans({
					wxAppId: this.wxAppId
				}).then(res => {
					this.$Modal.info({
						title: '请等待',
						content: '<p>粉丝同步中，请稍等...</p>'
					});
					console.log('this.data1', res)
				})
			},
			// 获取同步粉丝按钮状态
			handjudgeSynchronize() {
				indexApiModules.judgeSynchronize({
					wxAppId: this.wxAppId
				}).then(res => {
					this.judgeNum = res.data.isSynchronize
					console.log('this.data1', res)
				})
			},

			// 异常消息
			handgetFansAbnormalMsg(row) {
				const data = {
					page: this.fansMsgPage,
					limit: this.fansMsgLimit,
					wxId: row.wxId
				}
				indexApiModules.getFansAbnormalMsg(data).then(res => {
					this.fansMsglist = res.data.list
					this.fansMsgModal = true
					this.fansMsgTotal = res.data.pageData.count
					console.log('this.data1', this.data1)
				})
			},
			handfansMsgModal() {
				this.fansMsgModal = false;
				this.fansMsglist = [],
					this.fansMsgPage = 1,
					this.fansMsgLimit = 10,
					this.fansMsgTotal = 0
			},
			// 当前页
			handleCurrentChangeMsg(val) {
				this.fansMsgPage = val
				this.handgetWxFansList()
			},
			//每页数
			handleSizeChangeMsg(val) {
				this.fansMsgLimit = val;
				this.page = 1;
				this.handgetWxFansList()
			},




			handgetWxFansList() {
				const data = {
					page: this.page,
					limit: this.limit,
					wxAppId: this.wxAppId,
					startTime: this.startTime[0] ? this.startTime[0] : '',
					endTime: this.startTime[1] ? this.startTime[1] : '',
					// endTime: this.endTime,
					type: this.type,
					state: this.state,
					search: this.search,
				}
				indexApiModules.getWxFansList(data).then(res => {
					this.data1 = res.data.list
					this.data1.forEach(item => {
						item.isReply = Boolean(item.isReply)
						item.isPursuit = Boolean(item.isPursuit)
					})
					this.total = res.data.pageData.count
					console.log('this.data1', this.data1)
				})
			},
			handIsReplyChange(status, row) {
				let isReply = Number(status)
				indexApiModules.setFansReply({
					isReply: isReply,
					wxId: row.wxId,
				}).then(res => {
					console.log('res', res)
					this.$Message.success('修改成功');
					// this.handgetWxFansList()
				})
			},
			handIsPursuitChange(status, row) {
				let isPursuit = Number(status)
				indexApiModules.setFansPursuit({
					isPursuit: isPursuit,
					wxId: row.wxId,
				}).then(res => {
					console.log('res', res)
					this.$Message.success('修改成功');
					// this.handgetWxFansList()
				})
			},
			// 聊天记录
			handchatRecord(row) {
				this.$refs.chatbox.modalTrue(row)
			},

			// 选择时间
			getStartTime(val) {
				this.startTime = val
			},
			// 选择时间
			getEndTime(val) {
				this.endTime = val
			},
			// 当前页
			handleCurrentChange(val) {
				this.page = val
				this.handgetWxFansList()
			},
			//每页数
			handleSizeChange(val) {
				this.limit = val;
				this.page = 1;
				this.handgetWxFansList()
			},
		},
		created: function() {

		}
	};
</script>
<style lang="less" scoped>
	// primary按钮的颜色
	.ivu-btn-primary {
		background-color: #2d8cf0;
		border-color: #2d8cf0;
	}

	.fansManagement {
		font-size: 16px;
		color: #333333;

		.fansManagement_top {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 14px 16px;
			border-bottom: 1px solid #DDDDDD;


			.fansManagement_title {
				display: flex;
				align-items: center;

				.fansManagement_title_item {
					margin-right: 80px;
					color: #333333;
					font-size: 16px;
				}

				.color-blue {
					color: #2d8cf0;
				}
			}
		}

		.fansManagement_in {
			padding: 14px 16px;
			display: flex;

			.fansManagement_in_item {
				margin-right: 50px;

				span {
					margin-right: 15px;
				}

				button {
					margin-left: 15px;
				}
			}
		}


	}
</style>
<style lang="less">
	//此处修改了所有的table表格的下边框框线
	.ivu-table:before {
		height: 0;
	}

	.fansManagement_in_item_border {

		margin-right: 50px;

		.ivu-select-selection {
			border-radius: 4px 0 0 4px;
			border-right: none;
			margin-right: -1px;
		}

		.ivu-select-visible .ivu-select-selection {
			box-shadow: none;
		}

		.ivu-input {
			border-radius: 0 4px 4px 0;
		}
	}
</style>
